@import url(https://fonts.googleapis.com/css?family=Mukta:700);
body {
  margin: 0px;
  padding: 0px;
  font-family: yellow-design;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: Meticula;
  width: 100%;
  word-wrap: break-word;
}
@font-face {
  font-family: Meticula;
  src: url(/portfolio/static/media/Meticula.af1feba1.ttf)
}

.App {
  /* The height needs to be set to a fixed value for the effect to work.
   * 100vh is the full height of the viewport. */
  height: 100vh;
  /* The scaling of the images would add a horizontal scrollbar, so disable x overflow. */
  /* Enable scrolling on the page. */
  /* Set the perspective to 2px. This is essentailly the simulated distance from the viewport to transformed objects.*/
  -webkit-perspective: 2px;
          perspective: 2px;
  
  position: relative;
  width: 100%;
  margin: 0;
}

.section {
  /* Needed for children to be absolutely positioned relative to the parent. */
  /* The height of the container. Must be set, but it doesn't really matter what the value is. */
  height: calc(100vh - 40px);

  /* For text formatting. */
  align-items: center;
  justify-content: space-between;
  color: white;
  text-shadow: 0 0 5px #000;
}

.parallax::after {
  /* Display and position the pseudo-element */
  content: " ";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;

  /* Move the pseudo-element back away from the camera,
   * then scale it back up to fill the viewport.
   * Because the pseudo-element is further away, it appears to move more slowly, like in real life. *
  /* Force the background image to fill the whole element. */
  background-size: 100%;
  /* Keep the image from overlapping sibling elements. */
  z-index: -1;
}

/* The styling for the static div. */
.static {
  background: white;
}

/* Sets the actual background images to adorable kitties. This part is crucial. */
.bg1::after {
  background-image: -webkit-gradient(linear, left top, right top, from(rgba(237, 243, 245, 0.192)), to(rgba(0, 0, 0, 0.329))), url(/portfolio/static/media/fotis.8990aa42.jpg);
  background-image: linear-gradient(to right, rgba(237, 243, 245, 0.192), rgba(0, 0, 0, 0.329)), url(/portfolio/static/media/fotis.8990aa42.jpg);

  /* Full height */
  height: 100%; 

  /* Center and scale the image nicely */
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

#section1{
  height: calc(100vh - 40px);
}

