r{display:flex;flex-wrap:wrap;flex-direction:column;align-content:space-between}.introduction-container{position:absolute;top:50%;left:50%}@media screen and (max-width: 600px){.btn-container{position:relative;margin-top:-50px}.introduction-text{font-size:27px}}@media screen and (max-height: 800px){.btn-container{position:relative;margin-top:-50px}.introduction-text{font-size:27px}}

