.nav{
    font-family: Meticula;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    width: 100%;
    height: 40px;
    background-color:  rgb(27, 36, 47);
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 10;
    font-size: 1.8vh;
  }
  
.ul{
    display: flex;
    margin: 0px;
    height: 40px;
    color: white;
  }
.ul li{
    list-style: none;
    line-height: 40px;
    font-weight: bold;
    height: 40px;
  }
.ul li span{
    text-decoration: none;
    text-transform: uppercase;
    padding: 0 15px;
  }
  
.ul :hover{
    -webkit-transition: 0.3s;
    transition: 0.3s;
    color:  rgb(227,27,109);
  }
  
.ul :focus{
        -webkit-transition: 0.3s;
        transition: 0.3s;
        color:  rgb(227,27,109);
  }

  .li-active{
    color: rgb(227,27,109);
  }
  
